.notice_header {
  display: flex;
  align-items: center;
  position: relative;
  margin-bottom: 30px;
}

.notice_header .notice_tit {
  font-size: 24px;
  display: flex;
  align-items: center;
}

.notice_header .notice_tit li {
  display: inline-block;
  padding: 0;
  position: relative;
}

.notice_header .notice_tit li + li {
  padding-left: 20px;
  margin-left: 20px;
}

.notice_header .notice_tit li + li::before {
  content: "";
  display: inline-block;
  width: 1px;
  height: 20px;
  position: absolute;
  left: 0;
  top: 25%;
  background-color: #ccc;
}

.notice_header .notice_tit li a {
  display: inline-block;
  color: #999;
}

.notice_header .notice_tit li a.is-active {
  color: #000;
}

.notice_header .notice_tit li h4 {
  font-size: 30px;
  line-height: 45px;
  letter-spacing: -0.3px;
}

.notice_header .notice_tit_msg {
  display: block;
  height: 100%;
  position: relative;
}

.notice_header .notice_tit_msg .cnt {
  display: inline-block;
  font-size: 20px;
  line-height: 22px;
  letter-spacing: -0.2px;
  font-weight: 700;
  color: #4dac27;
  box-sizing: border-box;
  padding-left: 8px;
  position: absolute;
  top: 11px;
}

.notice_header .notice_btn {
  position: absolute;
  right: 0;
}

.notice_header .notice_btn .text {
  font-family: "NotoSansCJKKR";
  font-weight: 500;
}

.notice_search {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(80, 85, 177, 0.07);
  border-radius: 10px;
  padding: 20px 0;
  margin-bottom: 50px;
}

.notice_search .form_ip {
  width: 410px;
}

.notice_search .btn_ip {
  margin-left: 6px;
}

.notice_tbl_top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 11px;
}

.notice_tbl_top strong {
  font-size: 16px;
}

.notice_tbl_top strong span.total {
  color: #4dac27;
}

.notice_tbl_top strong span:last-child {
  margin-left: -3px;
}

.notice_tbl_top .form_sel {
  width: 120px;
}

.notice_tbl .tbl_col_line tr th {
  padding-bottom: 16px;
}

.notice_tbl .tbl_col_line tr.top_fixed .text {
  color: #5055b1;
}

.notice_tbl .tbl_col_line tr td {
  padding-top: 28px;
  padding-bottom: 28px;
  color: #767676;
}

.notice_tbl .tbl_col_line tr td .btn_text_link {
  display: inline-block;
}

.notice_tbl .tbl_col_line tr td .btn_text_link .text {
  text-decoration: none;
}

.notice_tbl .tbl_col_line tr .text {
  color: #000;
  display: inline-block;
}

.notice_tbl .tbl_col_line tr .search_word {
  color: #5055b1;
}

.no_data_notice p {
  color: #000;
  margin-bottom: 12px;
}

.no_data_notice .notice_no_data {
  margin-bottom: 30px;
}

.no_data_notice button.btn_sm {
  padding-left: 27px;
  padding-right: 27px;
}

.no_data_notice .text {
  font-family: NotoSansCJKKR;
  font-weight: 500;
  line-height: 1.69;
  letter-spacing: -0.13px;
}

.notice_detail_title {
  position: relative;
}

.notice_detail_title .sns_share_wrap {
  position: absolute;
  top: 0;
  right: 0;
}

.notice_detail_title .info_text span {
  display: inline-block;
  padding: 0 9px 0 0;
  position: relative;
  color: #767676;
}

.notice_detail_title .info_text span::after {
  content: "";
  display: inline-block;
  margin: 0;
  width: 1px;
  height: 9px;
  position: absolute;
  right: 0;
  top: 27%;
  background-color: #d5d5d5;
}

.notice_detail_title .info_text span:last-child {
  padding-left: 5px;
  margin: 0;
}

.notice_detail_title .info_text span:last-child::after {
  display: none;
}

.notice_detail_title .info_text span:first-child {
  padding-left: 0;
}

.notice_detail_wrap {
  padding: 30px 10px 40px;
  border-top: 1px solid #eaeaea;
  border-bottom: 1px solid #d5d5d5;
}

.file_down_wrap {
  margin-top: 40px;
}

.btn_wrap {
  display: flex;
  justify-content: center;
  padding-top: 70px;
}

.detail_list .detail_item {
  border-bottom: 1px solid #eaeaea;
  color: #000;
}

.detail_list .detail_item .detail_info,
.detail_list .detail_item a {
  display: inline-block;
  padding: 28px 20px;
}

.detail_list .detail_item .detail_info::after {
  content: "";
  display: inline-block;
  width: 9px;
  height: 9px;
  margin-left: 6px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 8px 5px;
  background-image: url(../images/common/icon_arrow.png);
}

.detail_list .detail_item.no-data > a > span {
  color: #767676;
}

.detail_list .detail_item_prev .detail_info::after {
  transform: rotate(180deg);
}

.msg_tbl {
  padding-top: 20px;
}

.msg_file_down a.btn_file_down .ico_file {
  width: 16px;
  height: 16px;
  background-image: url(../images/common/icon_link.png) !important;
  background-size: 16px 16px;
}

.new_badge {
  width: 20px;
  height: 20px;
  display: inline-block;
  margin-left: 7px;
  background: url(../images/common/icon_new@2x.png);
  background-size: 20px 20px;
  background-repeat: no-repeat;
  text-indent: -9999em;
}

.btn_text_link img {
  width: 20px;
  height: 20px;
  margin-left: 9px;
  margin-top: -2px;
}